import React from "react";
import "./PhotoSection.scss";

function Photo(props) {
  return (
    <div className="photo">
      <a href={props.source}>
        <img src={props.source} alt="" />
      </a>
    </div>
  );
}

function PhotoSection(props) {
  const listPhotos = props.data.map((x) => {
    return <Photo source={x.source} />;
  });

  return (
    <section>
      <div className="container">
        <h3 className="section-title">{props.section_title}</h3>
        <div className="grid">{listPhotos}</div>
      </div>
    </section>
  );
}

export default PhotoSection;
