import React from "react";
import { useState } from "react";

import "./Slider.scss";

function Slide(props) {
  return (
    <div className="slide">
      <p className="slide-content">"{props.content}"</p>
      <p className="slide-author">{props.author}</p>
    </div>
  );
}

function Slider(props) {
  const listSlides = props.data.map((x) => {
    return <Slide content={x.content} author={x.author} />;
  });

  const [slideIx, setSlideIx] = useState(0);

  function decrementSlideIx() {
    if (slideIx <= 0) {
      setSlideIx(2);
    } else {
      setSlideIx(slideIx - 1);
    }
    console.log(slideIx);
  }

  function incrementSlideIx() {
    if (slideIx >= 2) {
      setSlideIx(0);
    } else {
      setSlideIx(slideIx + 1);
    }
    console.log(slideIx);
  }

  return (
    <div className="slider">
      <div className="wrap">{listSlides[slideIx]}</div>

      <div className="prev" onClick={decrementSlideIx}>
        &#10094;
      </div>
      <div className="next" onClick={incrementSlideIx}>
        &#10095;
      </div>
    </div>
  );
}

export default Slider;
