import React, { useState } from "react";
import { Link } from "react-router-dom";

import "./Nav.scss";
import logo from "./logo_WR.jpg";
import fa_fb from "./facebook-square-brands.svg";
import fa_li from "./linkedin-brands.svg";
import bars from "./bars-solid.svg";

function Nav() {
  const [toggle, switchToggle] = useState(false);

  let switchToggleHandler = () => {
    switchToggle(!toggle);
    console.log("butonclicked " + toggle);
  };

  const [subtoggle, switchSubtoggle] = useState(false);

  const SubtoggleOn = () => {
    switchSubtoggle(true);
  };

  const SubtoggleOff = () => {
    switchSubtoggle(false);
  };

  return (
    <header>
      <div className="container">
        <nav>
          <Link to="/">
            <img
              src={logo}
              alt="logo"
              className="nav-logo"
              width={75}
              height={75}
            />
          </Link>
          <button onClick={switchToggleHandler} className="nav-toggler">
            <img
              style={{ background: "white" }}
              src={bars}
              alt="toggle-icon"
              className="nav-toggler-icon"
            />
          </button>
          <div className={toggle ? "nav-collapse active" : "nav-collapse"}>
            <ul className="nav-list">
              <li className="nav-item" onMouseEnter={SubtoggleOff}>
                <Link to="/" onClick={switchToggleHandler} className="nav-link">
                  Home
                </Link>
              </li>
              <li
                className="nav-item"
                onClick={switchToggleHandler}
                onMouseEnter={SubtoggleOn}
                style={{ fontStyle: "italic", cursor: "pointer" }}
              >
                <Link to="/partners">Partners</Link>
                {window.innerWidth > 768 ? (
                  <div
                    onMouseLeave={SubtoggleOff}
                    className={`nav-submenu ${subtoggle ? "active" : ""}`}
                  >
                    <li className="nav-submenu-item">
                      <Link to="/Bain">Bain</Link>
                    </li>
                    <li className="nav-submenu-item">
                      <Link to="/Cloudity">Cloudity</Link>
                    </li>
                    <li className="nav-submenu-item">
                      <Link to="/Kearney">Kearney</Link>
                    </li>
                  </div>
                ) : (
                  ""
                )}
              </li>

              <li className="nav-item" onMouseEnter={SubtoggleOff}>
                <Link
                  to="./event"
                  onClick={switchToggleHandler}
                  className="nav-link"
                >
                  Event
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  to="./register"
                  onClick={switchToggleHandler}
                  className="nav-link"
                >
                  Register
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  to="./FAQ"
                  onClick={switchToggleHandler}
                  className="nav-link"
                >
                  FAQ
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  to="./about"
                  onClick={switchToggleHandler}
                  className="nav-link"
                >
                  About Us
                </Link>
              </li>
              <li className="nav-item">
                <a href="https://www.facebook.com/thewarroomconference">
                  <img src={fa_fb} alt="" className="nav-icon" />
                </a>
              </li>
              <li className="nav-item">
                <a href="https://www.linkedin.com/company/the-warroom-warsaw-consulting-conference/">
                  <img src={fa_li} alt="" className="nav-icon" />
                </a>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    </header>
  );
}

export default Nav;
