import React from "react";
import "./Intro.scss";

function Intro(props) {
  const introStyle = {
    backgroundImage: "url(" + props.background + ")",
    color: props.black ? "black" : "white",
  };
  return (
    <section
      className={"intro " + (props.high ? "intro-high" : "")}
      style={introStyle}
    >
      <div className="container">
        <h1 className="intro-title">{props.title}</h1>
        <h2 className="intro-subtitle">{props.subtitle}</h2>
      </div>
    </section>
  );
}

export default Intro;
