import React from "react";
import { Link } from "react-router-dom";

import "./TeamMembers.scss";

function TeamMember(props) {
  let linkedin_icon = require("./linkedin-brands.svg");

  return (
    <>
      <div className="team-member">
        <img src={props.photo} alt={props.name} className="team-member-photo" />
        <hr className="team-member-hr" />
        <span className="team-member-title">{props.title}</span>
        <span className="team-member-subtitle">{props.subtitle}</span>
        <a href={props.linkedin} className="team-member-link">
          <img
            src={linkedin_icon}
            alt="LinkedIn"
            className="team-member-icon"
          />
        </a>
      </div>
    </>
  );
}

function TeamMembers(props) {
  const listTeamMembers = props.data.map((x) => {
    return (
      <TeamMember
        photo={x.photo}
        title={x.title}
        subtitle={x.subtitle}
        linkedin={x.linkedin}
      />
    );
  });

  return (
    <section>
      <div className="container">
        <div className="team">{listTeamMembers}</div>
      </div>
    </section>
  );
}

export default TeamMembers;
