import React from "react";
import { useState } from "react";

import "./Collapsible.scss";

function FaqElement(props) {
  const [active, setActive] = useState(false);

  return (
    <div className="faq-element">
      <h4
        className={`faq-element-question ${active ? "active" : ""}`}
        onClick={() => setActive(!active)}
      >
        {props.question}
      </h4>
      <hr className="faq-element-hr" />
      <div className={`faq-element-hide ${active ? "active" : ""}`}>
        <p className="faq-element-answer">{props.answer}</p>
      </div>
    </div>
  );
}

function Faq(props) {
  const faqElementList = props.data.map((x) => {
    return <FaqElement question={x.question} answer={x.answer} />;
  });

  return <>{faqElementList}</>;
}

export default Faq;
