import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.scss";

import { Helmet } from "react-helmet";

// import main components
import Nav from "./components/nav/Nav";
import Home from "./pages/home/Home";
import Partners from "./pages/partners/Partners";
import Event_C from "./pages/event/Event";
import Register from "./pages/register/Register";
import FAQ from "./pages/FAQ/FAQ";
import About from "./pages/about/About";
import NotFound from "./pages/notfound/NotFound";
import Footer from "./components/footer/Footer";

import { Bain } from "./pages/partners/Partners";
import { Cloudity } from "./pages/partners/Partners";
import { Kearney } from "./pages/partners/Partners";

let BG = require("./pages/home/home.jpg");

function App() {
  return (
    <>
      <Router>
        <Helmet>
          <meta charset="UTF-8" />
          <meta
            name="viewport"
            content="width=device-width, initial-scale=1.0"
          />
          <meta
            name="google-site-verification"
            content="zGnlNvZHUFIGho07V8WzRBNLzOHjHqeBxGwD9PQYJ4I"
          />
          <meta
            property="og:title"
            content="WARroom Conference | CEMS MIM | 2020"
          />
          <meta property="og:url" content="http://www.thewarroom.pl" />
          <meta
            property="og:description"
            content="The WARroom gathers CEMS MIM
    students from around the world to solve business case studies together,
    learn from consultants and exchange new ideas"
          />
          <meta property="og:image" content={BG} />

          <meta
            name="description"
            content="The WARroom gathers CEMS MIM students from around the world to solve business case studies together, learn from consultants and exchange new ideas"
          />
          <link rel="icon" sizes="192x192" href="/favicon.ico" />
          <title>WARroom Conference | CEMS MIM | 2020</title>
        </Helmet>
        <Nav />
        <main>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route path="/partners" component={Partners} />
            <Route path="/event" component={Event_C} />
            <Route path="/register" component={Register} />
            <Route path="/FAQ" component={FAQ} />
            <Route path="/about" component={About} />
            <Route path="/Bain" component={Bain} />
            <Route path="/Cloudity" component={Cloudity} />
            <Route path="/Kearney" component={Kearney} />
            <Route component={NotFound} />
          </Switch>
        </main>
        <Footer />
      </Router>
    </>
  );
}

export default App;
