import React from "react";
import { Link } from "react-router-dom";

import "./IconFields.scss";

function IconField(props) {
  return (
    <div className="iconfield">
      <div className="iconfield-wrap">
        <div className="iconfield-row">
          <i className="iconfield-icon">
            <img src={props.icon} alt="" />
          </i>
          <h4 className="iconfield-title">{props.title}</h4>
        </div>
        <p className="iconfield-content">{props.content}</p>
      </div>
      <div>
        <Link to={props.a_destination} className="iconfield-link">
          {props.a}
        </Link>
      </div>
    </div>
  );
}

function IconFields(props) {
  const listIconFields = props.data.map((x) => {
    return (
      <IconField
        icon={x.icon}
        title={x.title}
        content={x.content}
        a={x.a}
        a_destination={x.a_destination}
      />
    );
  });

  return (
    <div className="container">
      <h3 className={props.section_title ? "section-title" : ""}>
        {props.section_title}
      </h3>
      <div
        className="iconfields"
        style={{
          display: "grid",
          gridTemplateColumns: "repeat(" + props.data.length + ", 1fr)",
          gridGap: "3em",
        }}
      >
        {listIconFields}
      </div>
    </div>
  );
}

export default IconFields;
