import React from "react";

import Button from "./Button";

function CallToAction(props) {
  let bg_str = "";
  if (props.background.indexOf(".") !== -1) {
    bg_str = "url(" + props.background + ")";
  } else {
    bg_str = props.background;
  }

  return (
    <section
      className="cta"
      style={{
        background: bg_str,
        color: "white",
        textAlign: props.text_align,
      }}
    >
      <div className="container">
        <h3
          className="section-title"
          style={{
            fontSize: props.small_title ? "1em" : "2em",
            color: props.white_title ? "white" : "#FFCD33",
          }}
        >
          {props.title}
        </h3>
        <p className="cta-content">{props.content[0]}</p>
        <p className="cta-content">{props.content[1]}</p>
        <Button
          text={props.button_text}
          button_type={props.button_type}
          link={props.button_link}
        />
      </div>
    </section>
  );
}

export default CallToAction;
