import React from "react";

import Intro from "../../components/Intro";
import IconFields from "../../components/IconFields";
import CallToAction from "../../components/CallToAction";
import PhotoSection from "../../components/PhotoSection";

import "./Event.scss";
let BG = require("./event.jpg");
let ctaBG = require("./przekierowanie.jpg");
let harmonogram = require("./harmonogram.png");

function Event() {
  const icon_fields = [
    {
      icon: require("./icons/paste-solid.svg"),
      title: "Case studies",
      content:
        "The main goal of the case study is to show the students real-life business cases. Participants will solve the case under the guidance of consultants. ",
    },
    {
      icon: require("./icons/microphone-alt-solid.svg"),
      title: "Mock interview",
      content:
        "Our mock interviewâ€™s aim is to give participants an occasion to gain a fine professional experience.",
    },
    {
      icon: require("./icons/utensils-solid.svg"),
      title: "Rotation Dinner",
      content:
        "We will provide you with great entertainment and icebreaking opportunities to unwind after a day full of business case studies. There will be no place for boredom. ",
    },
  ];

  const description_icon_fields = [
    {
      icon: require("./icons/calendar-times-solid.svg"),
      title: "When",
      content: "24-26 November 2021",
    },
    {
      icon: require("./icons/map-marker-alt-solid.svg"),
      title: "Where",
      content: "Warsaw School of Economics",
    },
    {
      icon: require("./icons/user-solid.svg"),
      title: "Who",
      content: "CEMS MIM Students",
    },
  ];

  const uploadPhotos = () => {
    let photo_path = "";
    let object = "";
    let dict = [];
    for (let i = 0; i < 8; i++) {
      photo_path = `./old_photos/${i + 1}.jpg`;
      object = { source: [require("" + photo_path)] };
      dict.push(object);
    }
    return dict;
  };

  return (
    <>
      <Intro title="WARroom 2021" background={BG} />

      {/* Sekcja pierwsza - opis*/}
      <section className="description">
        <div className="container">
          <h3 className="section-title">The Event</h3>
          <div className="description-block">
            <h4 className="description-title">Warsaw Consulting Conference</h4>
            <p className="description-content">
              ... is a reputable Management Consulting Conference in Poland
              gathering highly-talented CEMS MIM students from leading business
              schools. During the event, Participants will be faced with
              challenging case studies as well as networking possibilities. The
              event offers a possibility to obtain CEMS Skill Seminars.
            </p>
          </div>
          <div className="description-block">
            <h4 className="description-title">
              Form of the Conference 2021
            </h4>
            <p className="description-content">
              In the light of the COVID-19 pandemic, the event will be online,
              which enables everyone to participate in it from anywhere in the
              world. Not only will participants take part in real business case
              studies organized by our corporate partners, but also they will
              have less formal occasions to interact.
            </p>
          </div>
          <IconFields data={description_icon_fields} />
        </div>
      </section>      <CallToAction
        background={ctaBG}
        title="Stay in touch"
        content={["For up to date information follow us on social media"]}
        button_text="Our Facebook"
        button_type="secondary"
        button_link="https://www.facebook.com/thewarroomconference"
        text_align="center"
      />
      <section>
        <IconFields section_title="How will it look like?" data={icon_fields} />
      </section>

      <PhotoSection
        section_title="The WARroom 2019 Highlights"
        data={uploadPhotos()}
      />
    </>
  );
}

export default Event;
